<template>
  <div class="publish-container" v-loading="uploadLoading">
    <div class="rules-area box-class" v-if="isEditPage&&stepNumber===1">
      <span class="title-class">服务选择</span>
      <el-form label-position="left" ref="serviceData" :model="serviceData" :rules="serviceRule" label-width="150px" size="small" v-if="isAdd">
        <el-form-item label="服务类别：" prop="classification">
          <el-select v-model="serviceData.classification" @change="classificationChange" placeholder="请选择服务类别">
            <el-option
              v-for="item in typeList"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="服务名称：" prop="strategyId">
          <el-select v-model="serviceData.strategyId" placeholder="请选择服务名称">
            <el-option
              v-for="item in typeData[serviceData.classification]"
              :key="item.strategyId"
              :label="item.name"
              :value="item.strategyId">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form label-position="left" :model="serviceData" label-width="120px" size="small" v-else>
        <el-form-item label="服务类别：">
          {{baseData.classification}}
        </el-form-item>
        <el-form-item label="服务名称：">
          {{baseData.name}}
        </el-form-item>
      </el-form>
    </div>
    <div class="rules-area box-class" v-if="isEditPage&&stepNumber===1">
      <span class="title-class">发布策略</span>
      <el-form label-position="left" ref="imeiData" :model="imeiData" :rules="imeiRules" label-width="150px" size="small" class="imei-form">
        <el-form-item label="客户端最低版本号：" prop="clientVersion" >
          <el-input  type="text" v-model.number="imeiData.clientVersion" placeholder="如：40100" maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="发布方式：" prop="type" >
          <el-radio-group v-model="imeiData.type">
            <el-radio :label="0">全量发布</el-radio>
            <el-radio :label="1">灰度发布</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="控制方式：" prop="greyTypeList" v-if="imeiData.type===1" >
          <el-checkbox-group v-model="imeiData.greyTypeList" size="small">
            <el-checkbox :label="0">IMEI控制</el-checkbox>
            <el-checkbox :label="1">用户标签</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="定向灰度：" prop="imeiGreyType" v-if="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===0||imeiData.greyTypeList[1]===0)" >
          <el-radio-group v-model="imeiData.imeiGreyType">
            <el-radio :label="0">指定IMEI</el-radio>
            <el-radio :label="1">屏蔽IMEI</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="上传IMEI方式：" prop="grayscaleDataType" v-if="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===0||imeiData.greyTypeList[1]===0)">
          <el-checkbox-group v-model="grayscaleDataTypes" size="small" @change="grayscaleDataTypeChange">
            <el-checkbox :label="1">手动填写(≤10个)</el-checkbox>
            <el-checkbox :label="2">上传文件(txt格式，每行一个IMEI码，请不要添加其他字符)</el-checkbox>
          </el-checkbox-group>
          <div class="time-box" v-if="grayscaleDataTypes[0]===1">
            <el-input v-model="imeiValue" placeholder="请输入IMEI" style="width:200px;margin-right: 20px;"></el-input>
            <el-button type="primary" round size="mini" @click="saveIMEI()">添加</el-button>
          </div>
          <template v-if="grayscaleDataTypes[0]===1">
            <el-tag v-for="(imei, index) in imeiList" :key="index" closable type="info" @close="deleteIMEI(index)" style="margin-right: 5px;">
              {{imei}}
            </el-tag>
          </template>
          <div v-if="grayscaleDataTypes[0]===2&&!this.excelFile">
            <a class='download' :href='downloadFileUrl' download="下载" title="下载" v-if="downloadFileName" target="_blank">{{downloadFileName}}</a>
          </div>
          <el-upload v-if="grayscaleDataTypes[0]===2" ref="upload" action="ceshi" style="width:400px" :http-request="getExcelFile" :before-remove="removeFile" :limit="1" :on-exceed="handleExceed">
            <el-button size="small" type="primary">点击上传</el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="尾数灰度：" v-if="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===0||imeiData.greyTypeList[1]===0)">
          <el-checkbox v-model="isMantissaList1">使用1个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList1" v-if="isMantissaList1">
            <el-checkbox v-for="n in 10" :label="getForNumber(n,1)" :key="getForNumber(n,1)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,1)}}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox v-model="isMantissaList2">使用2个尾数</el-checkbox>
          <el-checkbox-group v-model="mantissaList2" v-if="isMantissaList2">
            <el-checkbox v-for="n in 100" :label="getForNumber(n,2)" :key="getForNumber(n,2)" style="margin-left:0px !important;margin-right:20px !important">{{getForNumber(n,2)}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>

        <!-- 用户标签 -->
        <el-form-item label="用户标签" v-show="imeiData.type===1&&imeiData.greyTypeList.length>0&&(imeiData.greyTypeList[0]===1||imeiData.greyTypeList[1]===1)">
            <treeselect 
            :load-options="loadOptions" :max-height="500" placeholder="请选择或者输入关键字搜索" v-model="imeiData.tags" 
            :multiple="true" :options="tagTreeData" :clearable="true" :searchable="true">
            </treeselect>
        </el-form-item>
      </el-form>
    </div>
    <div class="box-class" v-if="isDetailpage&&bohuiReason">
      <div class="reason-area">
        <div style="font-size: 19px;font-weight: bold;">驳回原因</div>
        <div>{{bohuiReason}}</div>
      </div>
    </div>
    <div class="buttons-area box-class" v-if="isEditPage&&stepNumber!==3">
      <el-button v-if="stepNumber===1" @click="cancelEditButton()" type="primary" round class="button-class">取消编辑</el-button>
      <el-button v-if="stepNumber===1" @click="saveButton()" type="primary" round class="button-class">提交审核</el-button>
    </div>
  </div>
</template>

<script>
import global from "../service/global";
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";

export default {
  name: "publish-component",
  components: { Treeselect },
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    isAdd: {
      type: Boolean,
      default: false
    },
    isDetailpage: {
      type: Boolean,
      default: false
    },
    typeData: {},
    baseData: {}
  },
  data() {
    var weightValidate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入初始权值"));
      } else if (/^\d+$/.test(value)) {
        let newValue = Number(value);
        if (newValue >= 0 && newValue <= 50) {
          callback();
        } else {
          callback(new Error("请输入整数0-50"));
        }
      } else {
        callback(new Error("请输入整数0-50"));
      }
    };
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      tagTreeData: [],
      serviceData: {
        classification: '',
        name: ''
      },
      typeList: [
        'tips','卡片'
      ],
      defaultProps: {
        value: 'id',
        children: "children",
        label: "label"
      },
      grayscaleDataTypes: [],
      excelFileName: "",
      excelFile: "",
      uploadLoading: false,
      greyType: 1,
      imeiGreyType: 1,
      downloadFileName: '',
      downloadFileUrl: '',
      isMantissaList1: false,
      isMantissaList2: false,
      imeiList: [],
      imeiValue: "",
      imeiData: {
        grayscaleDataType: '',
        type: 1,
        clientVersion: '',
        greyTypeList: []
      },
      mantissaList1: [],
      mantissaList2: [],
      startTimeDisabled: false,
      testCardUrl: "",
      isExam: false,
      xiajiaDialogVisible: false,
      jinyongDialogVisible: false,
      isEditPage: true,
      examDialogVisible: false,
      testImgSrc: "",
      testDialogVisible: false,
      bohuiReason: "",
      cardPreview: "",
      expandPreview: "",
      holidayList: global.holidayList,
      //peopleLabels: [{ key: 1, value: "年轻人" }, { key: 2, value: "老年人" }],
      stepNumber: 1,
      weightData: {
        order: "",
        attenuationDuration: ""
      },
      serviceRule: {
        classification: [
          { required: true, message: "请选择服务类别", trigger: "change" }
        ],
        strategyId: [
          { required: true, message: "请选择服务名称", trigger: "change" }
        ]
      },
      imeiRules: {
        clientVersion: [
          { required: true, message: "请输入最低版本号", trigger: "blur" },
          { type: 'number', message: '版本号须为数字', trigger: 'blur' },
        ],
        type: [
          { required: true, message: "请选择发布方式", trigger: "change" }
        ],
        greyTypeList: [
          { required: true, message: "请选择控制方式", trigger: "change" }
        ],
        imeiGreyType: [
          { required: true, message: "请选择定向灰度", trigger: "change" }
        ]
      },
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  
  methods: {
    appendId(arr, id) {
      for (let i = 0; i < arr.length; i++) {
        if (id) {
          arr[i]["id"] = id + "_" + arr[i].key;
        } else {
          arr[i]["id"] = "" + arr[i].key;
        }
        if (arr[i]["children"]) {
          this.appendId(arr[i]["children"], arr[i]["id"]);
        }
      }
      return arr;
    },
    appendKeyId(arr, keyId) {
      for (let i = 0; i < arr.length; i++) {
        if (keyId) {
          arr[i]["keyId"] = keyId + "-" + i;
        } else {
          arr[i]["keyId"] = "" + i;
        }
        if (arr[i]["children"]) {
          appendKeyId(arr[i]["children"], arr[i]["keyId"]);
        }
      }
      return arr;
    },
    childrenToNull(children) {
      let arr = [];
      if (children) {
        for (let i = 0; i < children.length; i++) {
          arr[i] = {
            id: children[i]["id"],
            label: children[i]["label"],
            hasScore: children[i]["hasScore"]
          };
          if (children[i]["children"] && children[i]["children"].length > 0) {
            arr[i]["children"] = null;
          }
        }
      } else {
        arr = undefined;
      }

      return arr;
    },
    findChildById(tagData, keyId) {
      var arrs = keyId.split("_");

      var item = JSON.parse(JSON.stringify(tagData));
      let i = 0;
      for (; i < arrs.length; i++) {
        item = item[parseInt(arrs[i])]["children"];
      }

      return this.childrenToNull(item);
    },
    loadOptions({ action, parentNode, callback }) {
      if (action === "LOAD_CHILDREN_OPTIONS") {
        parentNode.children = this.findChildById(this.tagTreeData, parentNode.keyId);
        callback();
      }
    },
    classificationChange() {
      this.serviceData.name = "";
    },
    grayscaleDataTypeChange(arrary) {
      if (arrary.length > 1) {
        this.grayscaleDataTypes.shift();
      }
    },
    deleteIMEI(index) {
      this.imeiList.splice(index, 1);
    },
    saveIMEI() {
      if (this.imeiList.length > 9) {
        this.$confirm("10个以上IMEI码请通过文件上传", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (this.imeiList.includes(this.imeiValue)) {
        this.$confirm("IMEI码重复！", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      } else if (/^\d{15}$/.test(this.imeiValue)) {
        this.imeiList.push(this.imeiValue);
      } else {
        this.$confirm("IMEI码不符合要求！(15位数字)", "提示", {
          confirmButtonText: "确定",
          center: true,
          type: "warning"
        });
      }
    },
    //尾数
    getForNumber(number, type) {
      let newNumber = number - 1;
      if (type === 1) {
        return newNumber + '';
      } else {
        if (newNumber < 10) {
          return '0' + newNumber;
        } else {
          return newNumber + '';
        }
      }
    },
    //详情页面多文件上传提醒
    handleExceed() {
      this.$message.warning("一次只能上传一个文件，请先删掉当前文件");
    },
    removeFile() {
      this.excelFile = "";
      this.excelFileName = "";
    },
    //上传文件后回调
    getExcelFile(data) {
      this.excelFile = data.file;
      this.excelFileName = data.file.name;
    },
    //跳转数据页面
    goDataPage() {
      this.$emit("publish-event", {
        type: "data-page"
      });
    },
    //回到列表页面
    goHome() {
      this.$emit("publish-event", {
        type: "home-page"
      });
    },
    isEmpty(key) {
      if (typeof key === 'undefined' || key === '' || key === null) {
        return true;
      } else {
        return false;
      }
    },
    dataCheck(canEmpty) {
      let flag = false
      let isImei = this.imeiData.greyTypeList[0]===0||this.imeiData.greyTypeList[1]===0;
      this.isAdd&&this.$refs['serviceData'].validate((valid) => {
        flag = valid
      });
      this.$refs['imeiData'].validate((valid) => {
        flag = valid
      });
      if (!canEmpty) {
        //灰度方式选择
        if (this.imeiData.type === 1&&isImei) {
          let mantissaList1Ready = this.isMantissaList1 && this.mantissaList1.length > 0 ? true : false;
          let mantissaList2Ready = this.isMantissaList2 && this.mantissaList2.length > 0 ? true : false;
          let imeiListReady = this.imeiData.grayscaleDataType === 1 && this.imeiList.length > 0 ? true : false;
          let excelFileReady = this.imeiData.grayscaleDataType === 2 && this.excelFile ? true : false;
          if (!(mantissaList1Ready || mantissaList2Ready || imeiListReady || excelFileReady)) {
            this.$confirm("上传IMEI方式和尾数灰度至少选择一种！", "提示", {
              confirmButtonText: "确定",
              center: true,
              type: "warning"
            });
            return false;
          }
        }
      }
      return flag;
    },
    //保存操作
    async saveButton() {
      if(!this.dataCheck()){
        return false
      }
      let imeiFileUrl = await this.uploadExcel();

      let imeiEndNumsString = this.mantissaList1.concat(this.mantissaList2).join(',');
      let imeiDefineTypeList = [];
      if(imeiEndNumsString){
        imeiDefineTypeList.push(1)
      }
      if(imeiFileUrl||this.imeiList.length>0){
        imeiDefineTypeList.push(0)
      }
      let param = {
        classification: this.serviceData.classification,
        strategyId: this.serviceData.strategyId,
        clientVersion: this.imeiData.clientVersion,
        imeiFileUrl: this.grayscaleDataTypes[0]===2?imeiFileUrl:'',
        imeiFileName: this.excelFileName,
        type: this.imeiData.type,
        imeiDefineType: imeiDefineTypeList.join(','),
        greyType: this.imeiData.greyTypeList.join(','),
        imeiGreyType: this.imeiData.imeiGreyType,
        imeiManual: this.grayscaleDataTypes[0]===1?this.imeiList.join(','):'',
        imeiEndNums: imeiEndNumsString,
        tags: this.imeiData.tags&&this.imeiData.tags.join(','),
      }
      if(this.isAdd){
        this.$http
          .post("/scene/publish", param)
          .then(response => {
            this.uploadLoading = false;
            if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "创建发布成功！"
                });
                this.goHome()
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("操作失败！");
          });
      }else {
        this.$http
          .post("/scene/updatePublish", param)
          .then(response => {
            this.uploadLoading = false;
            if (response.data && response.data.code == "0") {
                this.$message({
                  type: "success",
                  message: "修改发布成功！"
                });
                this.goHome()
            } else {
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("操作失败！");
          });
      }
    },
    //上传excel文件
    async uploadExcel() {
      if(!this.excelFile){
        return null
      }
      let formData = new FormData();
      formData.append("file", this.excelFile);
      return new Promise((resolve) => {
        this.$http
          .post('/scene/uploadImeiFile', formData)
          .then(response => {
            if (response.data && response.data.code == 0) {
              let fileId = response.data.data.imeiFileUrl;
              resolve(fileId);
            } else {
              this.uploadLoading = false;
              if (response.data.msg) {
                this.$message.error(response.data.msg);
              } else {
                this.$message.error("操作失败！");
              }
              resolve('');
            }
          })
          .catch(() => {
            this.uploadLoading = false;
            this.$message.error("操作失败！");
            resolve('');
          });
      });
    },
    //取消编辑操作
    cancelEditButton() {
      this.$confirm("是否确定取消编辑?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "返回",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$emit("publish-event", {
            type: "home-page"
          });
        })
        .catch(() => { });
    },
    //获取数据xhr
    getDataXhr() {
      let param = {
        strategyId: this.baseData.strategyId
      };
      this.$http
        .post("/scene/getPublishDetail", param)
        .then(response => {
          if (
            response.data &&
            response.data.code == "0" &&
            response.data.data
          ) {
            let pubulishDTO = response.data.data;
            this.serviceData.classification = pubulishDTO.classification
            this.serviceData.strategyId = pubulishDTO.strategyId;
            this.imeiData.type = pubulishDTO.type?pubulishDTO.type:1;
            this.imeiData.imeiGreyType = pubulishDTO.imeiGreyType&&parseInt(pubulishDTO.imeiGreyType);
            this.imeiData.clientVersion = pubulishDTO.clientVersion;
            this.imeiData.greyTypeList = pubulishDTO.greyType?pubulishDTO.greyType.split(',').map(Number):[];
            this.imeiData.imeiDefineTypeList = pubulishDTO.imeiDefineType?pubulishDTO.imeiDefineType.split(',').map(Number):[];
            // 手动填写
            if(pubulishDTO.imeiManual){
              this.grayscaleDataTypes=[1]
              this.imeiList = pubulishDTO.imeiManual.split(',').map(Number);
            }
            // 上传文件
            if(pubulishDTO.fileUrl){
              this.grayscaleDataTypes=[2];
              this.imeiList = [];
              this.imeiData.imeiFileUrl = pubulishDTO.fileUrl;
              this.downloadFileName = pubulishDTO.fileName;
              this.downloadFileUrl = pubulishDTO.fileUrl;
            }
            this.imeiData.tags = pubulishDTO.tags&&pubulishDTO.tags.split(',');

            if(pubulishDTO.imeiEndNums){
              let imeiEndList = pubulishDTO.imeiEndNums.split(',');
              imeiEndList.forEach(e => {
                if(e.length===1){
                  this.mantissaList1.push(e)
                }else if(e.length===2){
                  this.mantissaList2.push(e)
                }
              });
              this.isMantissaList2 = this.mantissaList2.length>0?true:false;
              this.isMantissaList1 = this.mantissaList1.length>0?true:false;
            }
          }
        })
        .catch(() => {
          //console.log(error);
        });
    },
    getTagList() {
      this.$http
        .post("/scene/getTagTree", {})
        .then(response => {
          if (
            response.data.code === 0
          ) {
            let children = response.data.data.children;
            children = this.appendId(children);
            this.tagTreeData  = children;

          }
          })
        .catch(() => {
          //console.log(error);
        });
    }
  },
  //初始化请求页面数据
  mounted: function () {
    if (this.isCheckpage || this.isDetailpage) {
      this.isEditPage = false;
      this.stepNumber = 2;
    }
    if (typeof this.baseData.id !== 'undefined') {
      }
    this.getDataXhr();
    this.getTagList();
  },
  created: function () {
  }
};
</script>
<style>
.imei-form .el-form-item__content {
  width: 500px;
}
.imei-form .el-checkbox-group {
  width: 800px;
}
</style>

<style scoped>
.publish-container {
  min-height: 100%;
  width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 40px;
  padding-right: 40px;
  background-color: rgb(226, 226, 226);
  padding-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
}
.main-title {
  height: 40px;
  width: 100%;
  display: flex;
  font-size: 25px;
  font-weight: bold;
  margin-top: 10px;
}
.title-class {
  height: 40px;
  width: 100%;
  display: flex;
  font-size: 19px;
  font-weight: bold;
  margin-top: 30px;
}
.confirmtitle-class {
  height: 25px;
  width: 100%;
  display: flex;
  font-size: 17px;
  font-weight: bold;
  margin-top: 10px;
}
.title2-class {
  height: 65px;
