<template>
  <div class="body-container">
    <add-component v-if="isTableShow&&!isCheckPage" @table-event="tableEvent"></add-component>
    <header-component v-if="isTableShow" @header-event="headerEvent" :is-checkpage="isCheckPage"></header-component>
    <table-component v-if="isTableShow" :data-list="dataList" @table-event="tableEvent" :is-checkpage="isCheckPage"></table-component>
    <footer-component v-if="isTableShow" :data-total="dataTotal" :current-page="pageIndex" :page-size="pageSize" @footer-event="footerEvent"></footer-component>
    <publish-Component v-if="isPublishShow" :is-add="isAdd" :type-data="typeList" :base-data="baseData" @publish-event="publishEvent" :is-detailpage="isDetailPage" :is-checkpage="isCheckPage"></publish-Component>
  </div>
</template>

<script>
import tableComponent from "./components/TableComponent.vue";
import headerComponent from "./components/HeaderComponent.vue";
import addComponent from "./components/AddComponent.vue"
import footerComponent from "./components/FooterComponent.vue";
import publishComponent from "./components/PubulishComponent.vue";

export default {
  name: "app",
  components: {
    addComponent,
    tableComponent,
    headerComponent,
    footerComponent,
    publishComponent
  },
  data() {
    return {
      isPublishShow: false,
      isDetailPage: false,
      isTableShow: true,
      isCheckPage: false,
      isAdd: false,
      typeList: [

      ],
      dataList: [
      ],
      pageIndex: 1,
      pageSize: 20,
      dataTotal: 0,
      searchName: "",
      searchType: "",
      searchModifyUserName:'',
      searchState: "",
      searchField: "",
      searchOrder: "",

      baseData: {}
    };
  },
  methods: {
    //活动管理
    publishEvent(event) {
      switch (event.type) {
        case "home-page":
          this.getDataXhr();
          this.isPublishShow = false;
          this.isTableShow = true;
          break;
        case "data-page":
          //跳转大数据页面
          break;
        default:
          break;
      }
    },
    //表格事件
    tableEvent(event) {
      switch (event.type) {
        case "data-page":
          //跳转数据页面
          break;
        case "add-page":
          this.baseData = {};
          this.isTableShow = false;
          this.isAdd = true;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isPublishShow = true;
          break;
        case "edit-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = false;
          this.isPublishShow = true;
          break;
        case "detail-page":
          this.baseData = event.data;
          this.isTableShow = false;
          this.isAdd = false;
          this.isDetailPage = true;
          this.isCheckPage = false;
          this.isPublishShow = true;
          break;
        case "check-page":
          this.baseData = event.data;
          this.isAdd = false;
          this.isTableShow = false;
          this.isDetailPage = false;
          this.isCheckPage = true;
          this.isPublishShow = true;
          break;
        case "refresh-data":
          this.getDataXhr();
          break;
        case "sortChange":
          this.searchOrder = event.data.order;
          this.searchField = event.data.field;
          this.getDataXhr();
          break;
        default:
          break;
      }
    },
    //页头事件
    headerEvent(event) {
      this.searchData(event.data);
    },
    //搜索请求
    searchData(data) {
      this.searchName = data.name;
      this.searchType = data.classification;
      if (this.isCheckPage) {
        this.searchState = 3;
      } else {
        this.searchState = data.status;
      }
      this.searchModifyUserName = data.modifyUserName;
      this.pageIndex = 1;
      this.getDataXhr();
    },
    //获取数据xhr
    getDataXhr() {
      var param = {
        name: this.searchName === "" ? undefined : this.searchName,
        status: this.searchState === "" ? undefined : this.searchState,
        classification: this.searchType === "" ? undefined : this.searchType,
        modifyUserName: this.searchModifyUserName === "" ? undefined : this.searchModifyUserName,
        field: this.searchField === "" ? undefined : this.searchField,
        order: this.searchOrder === "" ? undefined : this.searchOrder,
        page: this.pageIndex,
        size: this.pageSize
      };
      this.$http
        .post("/scene/publishList", param)
        .then(response => {
          this.dataTotal =
            response.data.data && response.data.data.totalElements;
          this.dataList = response.data.data && response.data.data.content;
        })
        .catch(() => {
          //console.log(error);
        });
    },
    //获取分类和名称
    getTypeList() {
      this.$http
        .post("/scene/getPublishExamine", {})
        .then(response => {
          this.typeList = response.data.data && response.data.data;
          
      })
      .catch(() => {
        //console.log(error);
      });
    },
    //页脚事件
    footerEvent(event) {
      switch (event.type) {
        case "pageChange":
          this.pageIndex = event.data;
          this.getDataXhr();
          break;
        case "sizeChange":
          this.pageIndex = 1;
          this.pageSize = event.data;
          this.getDataXhr();
          break;
        default:
          break;
      }
    }
  },
  created: function () {
    this.getDataXhr();
    this.getTypeList();
  }
};
</script>

<style scoped>
.body-container {
  min-height: 100%;
  padding-left: 70px;
  padding-right: 70px;
}
</style>
