<template>
  <div class="table">
    <el-table :data="dataList" border style="width: 100%" @sort-change="handleSortChange" height="600">
      <el-table-column prop="classification" label="类别" min-width="80" sortable="custom">
      </el-table-column>
      <el-table-column label="名称" min-width="180" prop="name">
      </el-table-column>
      <el-table-column prop="status" label="状态" min-width="120">
        <template slot-scope="scope">
          <span v-if="scope.row.status===0" style="color:Orange">待发布</span>
          <span v-if="scope.row.status===1" style="color:blue">发布审核</span>
          <span v-if="scope.row.status===2" style="color:Orange">待启动</span>
          <span v-if="scope.row.status===3" style="color:black">驳回</span>
          <span v-if="scope.row.status===4" style="color:green">灰度</span>
          <span v-if="scope.row.status===5" style="color:green">全量发布</span>
          <span v-if="scope.row.status===6" style="color:black">暂停中</span>
          <span v-if="scope.row.status===7" style="color:black">已失效</span>
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="修改时间" min-width="170" sortable="custom">
      </el-table-column>
      <el-table-column prop="modifyUserName" label="修改人" min-width="210">
      </el-table-column>
      <el-table-column label="操作" min-width="270">
        <template slot-scope="scope">
          <el-button v-if="scope.row.status===0||scope.row.status===3||scope.row.status===4||scope.row.status===5||scope.row.status===6" @click="goEditPage(scope.row)" size="mini" type="primary">修改发布</el-button>
          <el-button v-if="scope.row.status===4||scope.row.status===5||scope.row.status===6" @click="updateStatus(scope.row,7)" size="mini" type="primary">失效</el-button>
          <el-button v-if="scope.row.status===4||scope.row.status===5" @click="updateStatus(scope.row,6)" size="mini" type="warning">暂停</el-button>
          <el-button v-if="scope.row.status===2||scope.row.status===6" @click="updateStatus(scope.row,-1)" size="mini" type="success">启动</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="暂停申请" :visible.sync="zantingDialogVisible" width="600px" :before-close="zantingDialogClose" center>
      <el-form :model="zantingData" ref="zantingData" label-width="100px">
        <el-form-item label="暂停原因" prop="reason" :rules="applyRules">
          <el-input v-model="zantingData.reason" :autosize="{ minRows: 2, maxRows: 3}" type="textarea" placeholder="请填写暂停原因" maxlength="100"></el-input>
        </el-form-item>
      </el-form>
      <div style="display: flex;justify-content: flex-end;">{{zantingData.reason?zantingData.reason.length:0}}/100</div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="zantingDialogClose(true)">取 消</el-button>
        <el-button type="primary" @click="zantingDialogClose()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "table-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    var specialValidate = (rule, value, callback) => {
      if (/^\s+$/.test(value)) {
        callback(new Error("不能输入全空格"));
      } else {
        callback();
      }
    };
    return {
      zantingDialogVisible: false,
      zantingData: {},
      xiajiaDialogVisible: false,
      xiajiaData: {},
      applyRules: [
        { required: true, message: "内容不能为空", trigger: "blur" },
        { required: true, validator: specialValidate, trigger: "blur" }
      ]
    };
  },
  methods: {
    updateStatus(row,status) {
      if(status===-1){
        //先判断什么类型
        if(row.type===0){
          status = 5
        }else if(row.type===1) {
          status = 4
        }
      }
      let param = {
        strategyId: row.strategyId,
        status: status
      }
      this.$http
      .post("/scene/updateStatus", param)
      .then(response => {
        if (response.data && response.data.code == "0") {
          this.refreshData();
          this.$message({
            type: "success",
            message: "操作成功!"
          });
        } else {
          if (response.data.msg) {
            this.$message.error(response.data.msg);
          } else {
            this.$message.error("操作失败！");
          }
        }
      })
      .catch(() => {
        this.$message.error("删除失败！");
      });

    },
    //删除操作
    handleDelete(data) {
      let message = "是否确定删除“" + data.name + "”的草稿？";
      this.$confirm(message, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let param = {
            id: data.id,
            versionCode: data.versionCode
          };
          this.$http
            .post("/scene/del", param)
            .then(response => {
              if (response.data && response.data.code == "0") {
                this.refreshData();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              } else {
                if (response.data.msg) {
                  this.$message.error(response.data.msg);
                } else {
                  this.$message.error("删除失败！");
                }
              }
            })
            .catch(() => {
              this.$message.error("删除失败！");
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //跳转数据浏览页面
    goDataPage(data) {
      this.$emit("table-event", {
        type: "data-page",
        data: data
      });
    },
    //跳转编辑页面
    goEditPage(data) {
      this.$emit("table-event", {
        type: "edit-page",
        data: data
      });
    },
    //禁用弹窗
    zantingDialogClose(done) {
      if (typeof done === "function") {
        this.zantingData = {};
        done();
      } else if (done) {
        this.zantingData = {};
        this.zantingDialogVisible = false;
      } else {
        this.$refs["zantingData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.zantingData.id,
              versionCode: this.zantingData.versionCode,
              lastState: this.zantingData.status,
              status: -1,
              explain: this.zantingData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: "禁用成功"
                  });
                  this.xiajiaData = {};
                  this.zantingDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("禁用失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("禁用失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //禁用操作
    zanting(data) {
      this.zantingData = data;
      this.zantingDialogVisible = true;
    },
    //下架弹窗
    xiajiaDialogClose(done) {
      if (typeof done === "function") {
        this.xiajiaData = {};
        done();
      } else if (done) {
        this.xiajiaData = {};
        this.xiajiaDialogVisible = false;
      } else {
        this.$refs["xiajiaData"].validate(valid => {
          if (valid) {
            let param = {
              id: this.xiajiaData.id,
              versionCode: this.xiajiaData.versionCode,
              lastState: this.xiajiaData.status,
              status: 12,
              explain: this.xiajiaData.reason
            };
            this.$http
              .post("/scene/modifyState", param)
              .then(response => {
                if (response.data && response.data.code == "0") {
                  this.refreshData();
                  this.$message({
                    type: "success",
                    message: "下架成功"
                  });
                  this.xiajiaData = {};
                  this.xiajiaDialogVisible = false;
                } else {
                  if (response.data.msg) {
                    this.$message.error(response.data.msg);
                  } else {
                    this.$message.error("下架失败！");
                  }
                }
              })
              .catch(() => {
                this.$message.error("下架失败！");
              });
          } else {
            this.$message.warning("请按要求填写");
            return false;
          }
        });
      }
    },
    //下架操作
    xiajia(data) {
      this.xiajiaData = data;
      this.xiajiaDialogVisible = true;
    },
    // shangjiaDialogClose(done) {
    //   if (typeof done === "function") {
    //     this.shangjiaData = {};
    //     done();
    //   } else if (done) {
    //     this.shangjiaData = {};
    //     this.shangjiaDialogVisible = false;
    //   } else {
    //   }
    // },
    //上架操纵
    shangjia(data) {
      let param = {
        id: data.id,
        versionCode: data.versionCode,
        lastState: data.status,
        status: 9
      };
      this.$http
        .post("/scene/modifyState", param)
        .then(response => {
          if (response.data && response.data.code == "0") {
            this.refreshData();
            this.$message({
              type: "success",
              message: "上架成功"
            });
          } else {
            if (response.data.msg) {
              this.$message.error(response.data.msg);
            } else {
              this.$message.error("上架失败！");
            }
          }
        })
        .catch(() => {
          this.$message.error("上架失败！");
        });
    },
    //跳转详情页面
    goDetailPage(data) {
      this.$emit("table-event", {
        type: "detail-page",
        data: data
      });
    },
    //跳转审核页面
    goCheckPage(data) {
      this.$emit("table-event", {
        type: "check-page",
        data: data
      });
    },
    //刷新数据
    refreshData() {
      this.$emit("table-event", {
        type: "refresh-data"
      });
    },
    //排序事件
    handleSortChange(data) {
      let field = data.prop;
      let order = "";
      if (data.order === "ascending") {
        order = "asc";
      } else if (data.order === "descending") {
        order = "desc";
      }
      this.$emit("table-event", {
        type: "sortChange",
        data: {
          field: field,
          order: order
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.table {
  width: 100%;
  margin-top: 30px;
}
.shangjia-div {
  display: flex;
  flex-direction: column;
}
.text-line {
  display: flex;
  align-items: center;
}
.shangjia-date {
  margin-left: 7px;
}
.shangjia-text1 {
  color: rgb(192, 0, 0);
  margin-bottom: 20px;
}
.shangjia-text2 {
  font-size: 10px;
  color: rgb(196, 196, 196);
  margin-bottom: 20px;
}
</style>