/* eslint-disable */
<template>
  <div class="header-class">
    <div class="left-input">
      <el-input placeholder="名称" v-model="activityName" clearable></el-input>
    </div>
    <el-select v-model="typeKey" clearable placeholder="类别" class="left-select">
      <el-option v-for="item in typeList" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>
    <el-select v-model="statusKey" clearable placeholder="状态" class="left-select">
      <el-option v-for="item in statusList" :key="item.key" :label="item.name" :value="item.key">
      </el-option>
    </el-select>
    <el-select v-model="modifyUserName" clearable placeholder="修改人" class="left-select">
      <el-option v-for="item in nameList" :key="item" :label="item" :value="item">
      </el-option>
    </el-select>
    <el-button @click="searchData" icon="el-icon-search" class="left-button" type="primary">搜索</el-button>
  </div>
</template>

<script>
export default {
  name: "header-component",
  props: {
    isCheckpage: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      nameList: [],
      modifyUserName: '',
      activityName: "",
      typeKey: "",
      statusKey: "",
      typeList: ["卡片", "tips"],
      statusList: [
        { key: 0, name: "待发布" },
        { key: 1, name: "发布审核" },
        { key: 2, name: "待启动" },
        { key: 3, name: "驳回" },
        { key: 4, name: "灰度" },
        { key: 5, name: "全量发布" },
        { key: 6, name: "暂停中" },
        { key: 7, name: "已失效" }
      ]
    };
  },
  mounted: function () {

    this.getModifyNameList();
  },
  methods: {
    searchData() {
      this.$emit("header-event", {
        type: "search-event",
        data: {
          name: this.activityName,
          classification: this.typeKey,
          modifyUserName: this.modifyUserName,
          status: this.statusKey
        }
      });
    },
    getModifyNameList() {
      this.$http
        .post("/scene/getModifyUserName", {})
        .then(response => {
          if (
            response.data.code === 0
          ) {
            this.nameList  = response.data.data;

          }
          })
        .catch(() => {
          //console.log(error);
        });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header-class {
  width: 100%;
  height: 50px;
  margin-top: 30px;
}
.left-input {
  float: left;
  margin-left: 10px;
  width: 200px;
}
.left-button {
  float: left;
  margin-left: 20px;
}
.left-select {
  float: left;
  margin-left: 20px;
  width: 120px;
}
</style>